.icon {
	position: relative;
	display: block;
	width: 150px;
    height: 105px;
	font-size: 1rem; /* control icon size here */
	color: $lblue;
	margin: 0 auto; margin-top: 10px;
}

.cloud {
	position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 3.6875em;
    height: 3.6875em;
    margin: -1.84375em;
    background: $lblue;
    border-radius: 50%;
    box-shadow: -2.1875em 0.6875em 0 -0.6875em, 2.0625em 0.9375em 0 -0.9375em, 0 0 0 0.375em #fff, -2.1875em 0.6875em 0 -0.3125em #fff, 2.0625em 0.9375em 0 -0.5625em #fff;
	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: -0.5em;
		display: block;
		width: 4.5625em;
		height: 1em;
		background: $lblue;
		box-shadow: 0 0.4375em 0 -0.0625em #fff;
	}
	&:nth-child(2) {
		z-index: 0;
		background: $lblue;
		box-shadow:
			-2.1875em 0.6875em 0 -0.6875em $lblue,
			2.0625em 0.9375em 0 -0.9375em $lblue,
			0 0 0 0.375em $lblue,
			-2.1875em 0.6875em 0 -0.3125em $lblue,
			2.0625em 0.9375em 0 -0.5625em $lblue;
		opacity: 0.3;
		transform: scale(0.5) translate(6em, -3em);
		animation: cloud 4s linear infinite;
		&:after { background: $lblue; }
	}
}

.sun {
	position: absolute;
	top: 50%; left: 50%;
	width: 2.5em; height: 2.5em;
	margin: -1.25em;
	background: transparent;
	border-radius: 50%;
	box-shadow: 0 0 0 0.375em $lblue;
	animation: spin 12s infinite linear;
}

.rays {
	position: absolute;
	top: -2em; left: 50%;
	display: block;
	width: 0.375em; height: 1.125em;
	margin-left: -0.1875em;
	background: $lblue;
	border-radius: 0.25em;
	box-shadow: 0 5.375em $lblue;
	&:before, &:after {
		content: '';
		position: absolute;
		top: 0em; left: 0em;
		display: block;
		width: 0.375em; height: 1.125em;
		transform: rotate(60deg);
		transform-origin: 50% 3.25em;
		background: $lblue;
		border-radius: 0.25em;
		box-shadow: 0 5.375em $lblue;
	}
	&:before { transform: rotate(120deg); }
}

.cloud + .sun { margin: -2em 1em; }

.rain, .lightning, .snow {
	position: absolute;
	z-index: 2;
	top: 50%; left: 50%;
	width: 3.75em; height: 3.75em;
	margin: 0.375em 0 0 -2em;
	background: transparent;
}

.rain:after {
	content: '';
	position: absolute;
	z-index: 2;
	top: 50%; left: 50%;
	width: 1.125em; height: 1.125em;
	margin: -1em 0 0 -0.25em;
	background: $green;
	border-radius: 100% 0 60% 50% / 60% 0 100% 50%;
	box-shadow:
		0.625em 0.875em 0 -0.125em rgba(255,255,255,0.2),
		-0.875em 1.125em 0 -0.125em rgba(255,255,255,0.2),
		-1.375em -0.125em 0 rgba(255,255,255,0.2);
	transform: rotate(-28deg);
	animation: rain 3s linear infinite;
}

.bolt {
	position: absolute;
	top: 50%; left: 50%;
	margin: -0.25em 0 0 -0.125em;
	color: $lblue;
	opacity: 0.3;
	animation: lightning 2s linear infinite;
	&:nth-child(2) {
		width: 0.5em;
		height: 0.25em;
		margin: -1.75em 0 0 -1.875em;
		transform: translate(2.5em, 2.25em);
		opacity: 0.2;
		animation: lightning 1.5s linear infinite;
		&:before {
			margin: -0.75em 0 0 -0.5em;
			border-top: 0.625em solid transparent;
			border-right: 0.375em solid;
			border-bottom: 0.375em solid;
			border-left: 0.25em solid transparent;
		}
		&:after {
			margin: -0.125em 0 0 -0.125em;
			border-top: 0.375em solid;
			border-right: 0.25em solid transparent;
			border-bottom: 0.625em solid transparent;
			border-left: 0.375em solid;
		}
	}
	&:before, &:after {
		content: '';
		position: absolute;
		z-index: 2;
		top: 50%; left: 50%;
		margin: -1.625em 0 0 -1.0125em;
		border-top: 1.25em solid transparent;
		border-right: 0.75em solid;
		border-bottom: 0.75em solid;
		border-left: 0.5em solid transparent;
		transform: skewX(-10deg);
	}
	&:after {
		margin: -0.25em 0 0 -0.25em;
		border-top: 0.75em solid;
		border-right: 0.5em solid transparent;
		border-bottom: 1.25em solid transparent;
		border-left: 0.75em solid;
		transform: skewX(-10deg);
	}
}

.flake:before, .flake:after {
	content: '\2744';
	position: absolute;
	top: 50%; left: 50%;
	margin: -1.025em 0 0 -1.0125em;
	color: $lblue;
	list-height: 1em;
	opacity: 0.2;
	animation: spin 8s linear infinite reverse;
}
.flake:after {
	margin: 0.125em 0 0 -1em;
	font-size: 1.5em;
	opacity: 0.4;
	animation: spin 14s linear infinite;
}
.flake:nth-child(2):before {
	margin: -0.5em 0 0 0.25em;
	font-size: 1.25em;
	opacity: 0.2;
	animation: spin 10s linear infinite;
}
.flake:nth-child(2):after {
	margin: 0.375em 0 0 0.125em;
	font-size: 2em;
	opacity: 0.4;
	animation: spin 16s linear infinite reverse;
}

/* Animations */ 

@keyframes spin {
  100% { transform: rotate(360deg); }
}

@keyframes cloud {
  0% { opacity: 0; }
  50% { opacity: 0.3; }
  100% {
    opacity: 0;
    transform: scale(0.5) translate(-200%, -3em);
  }
}

@keyframes rain {
  0% {
    background: $green;
    box-shadow:
      0.625em 0.875em 0 -0.125em rgba(255,255,255,0.2),
      -0.875em 1.125em 0 -0.125em rgba(255,255,255,0.2),
      -1.375em -0.125em 0 $green;
  }
  25% {
    box-shadow:
      0.625em 0.875em 0 -0.125em rgba(255,255,255,0.2),
      -0.875em 1.125em 0 -0.125em $green,
      -1.375em -0.125em 0 rgba(255,255,255,0.2);
  }
  50% {
    background: rgba(255,255,255,0.3);
    box-shadow:
      0.625em 0.875em 0 -0.125em $green,
      -0.875em 1.125em 0 -0.125em rgba(255,255,255,0.2),
      -1.375em -0.125em 0 rgba(255,255,255,0.2);
  }
  100% {
    box-shadow:
      0.625em 0.875em 0 -0.125em rgba(255,255,255,0.2),
      -0.875em 1.125em 0 -0.125em rgba(255,255,255,0.2),
      -1.375em -0.125em 0 $green;
  }
}

@keyframes lightning {
  45% {
    color: $lblue;
    background: $lblue;
    opacity: 0.2;
  }
  50% {
    color: $green;
    background: $green;
    opacity: 1;
  }
  55% {
    color: $lblue;
    background: $lblue;
    opacity: 0.2;
  }
}